html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

:root {
  --pale-blue: hsl(225, 100%, 94%);
  --bright-blue: hsl(245, 75%, 52%);
  --very-pale: hsl(225, 100%, 98%);
  --desaturated-blue: hsl(224, 23%, 55%);
  --dark-blue: hsl(223, 47%, 23%);
}

:root {
  --red-hat-display: "Red Hat Display", sans-serif;
}

p {
  font-size: 1rem;
}

.container {
  padding: 0 1.25rem;
  max-width: 75rem;
  margin: 0 auto;
}
@media (min-width: ) {
  .container {
    padding: 0;
  }
}